.workflow .connection {
  position: absolute;
  transform: translate(9999px, 9999px); }

.workflow .connection .main-path {
  fill: none;
  stroke-width: 5px;
  stroke: #4682b4;
  transform: translate(-9999px, -9999px); }

.workflow .connection .main-path:hover {
